import * as React from "react";
import type { WorkflowStep } from "../WorkflowSplitView.types";

export type BreadcrumbsBarProps<T> = {
	steps: WorkflowStep<T>[];
	currentIndex: number;
	onNavigate?: (index: number) => void;
	renderLabel?: (step: WorkflowStep<T>, index: number) => React.ReactNode;
	ariaLabel?: string;
	className?: string;
};

export function BreadcrumbsBar<T>({
	steps,
	currentIndex,
	onNavigate,
	renderLabel,
	ariaLabel = "Breadcrumbs",
	className,
}: BreadcrumbsBarProps<T>) {
	return (
		<nav aria-label={ariaLabel} className={className}>
			<ol className="nhsfdp-breadcrumbs" role="list">
				{steps.map((s, i) => {
					const isCurrent = i === currentIndex;
					const label = renderLabel
						? renderLabel(s, i)
						: (s.label ?? `Step ${i + 1}`);
					return (
						<li key={i} className={isCurrent ? "is-current" : undefined}>
							{isCurrent ? (
								<span aria-current="page">{label}</span>
							) : (
								<button
									type="button"
									className="nhsfdp-breadcrumb"
									onClick={() => onNavigate?.(i)}
								>
									{label}
								</button>
							)}
						</li>
					);
				})}
			</ol>
		</nav>
	);
}
