import * as React from "react";
import type { WorkflowStep } from "../WorkflowSplitView.types";

export type CardsScrollerProps<T> = {
	steps: WorkflowStep<T>[];
	currentIndex: number;
	onNavigate?: (index: number) => void;
	renderCard: (
		step: WorkflowStep<T>,
		index: number,
		isCurrent: boolean
	) => React.ReactNode;
	className?: string;
	ariaLabel?: string;
};

export function CardsScroller<T>({
	steps,
	currentIndex,
	onNavigate,
	renderCard,
	className,
	ariaLabel = "Steps",
}: CardsScrollerProps<T>) {
	const containerRef = React.useRef<HTMLDivElement | null>(null);

	React.useEffect(() => {
		const el = containerRef.current;
		if (!el) return;
		const child = el.children[currentIndex] as HTMLElement | undefined;
		child?.scrollIntoView({
			behavior: "smooth",
			inline: "center",
			block: "nearest",
		});
	}, [currentIndex]);

	return (
		<div className={className} aria-label={ariaLabel} role="region">
			<div ref={containerRef} className="nhsfdp-cards-scroller" tabIndex={0}>
				{steps.map((s, i) => (
					<div
						key={i}
						className={
							"nhsfdp-card" + (i === currentIndex ? " is-current" : "")
						}
						role="group"
						aria-roledescription="step"
						aria-label={(s.label ?? `Step ${i + 1}`).toString()}
						onClick={() => onNavigate?.(i)}
					>
						{renderCard(s, i, i === currentIndex)}
					</div>
				))}
			</div>
		</div>
	);
}
